import React from 'react';

//Logo's
import csharpLogo from '../Assets/Logos/c-sharp-logo.png'
import cppLogo from '../Assets/Logos/C++-logo.png'
import pythonLogo from '../Assets/Logos/python-logo.png'
import jsLogo from '../Assets/Logos/js-logo.png'
import css3Logo from '../Assets/Logos/css3-logo.png'
import htmlLogo from '../Assets/Logos/html-logo.png'

import man from '../Assets/man.gif'

import './home.css'

function Home(){    
    return <div>
        <div className="bio" style={{height: '110px', marginTop: '10px'}}>
            <h1 style={{margin: '-20px'}}>Marcus Nordström</h1>
            <h3 style={{margin: '20px'}}>Programmer</h3>

            <div className='icons'>    
                <ul>
                    <li>
                        <img className="small-logo" src={cppLogo}></img>
                    </li>
                    <li>
                        <img className="small-logo" src={pythonLogo}></img>
                    </li>
                    <li>
                        <img className="small-logo" src={jsLogo}></img>
                    </li>
                    <li>
                        <img className="small-logo" src={css3Logo}></img>
                    </li>
                    <li>
                        <img className="small-logo" src={htmlLogo}></img>
                    </li>
                    <li>
                        <img className="small-logo" src={csharpLogo}></img>
                    </li>
                </ul>
            </div>
        </div>

        <div className="info">
            <p> Hello I am Marcus, I have been programming for about 5 years now.<br></br>
                I am self-taught by heart. Started programming at my free time out of pure interest.<br></br>
                When I was around 12 years old I sat in MS-Dos and started Grand Theft Auto, one of the first games I ever played.
            </p>
            <p>
                I have done websites with Angular and React, Using API's, a bit of database with MySQL and SQLite.<br></br>
                Software using everything from C++, Python, C# , to other languages.<br></br>
                Games using Unity, Unreal, Godot, and I have also been touching the topic of game engines using OpenGL, SDL, Vulkan, and a lot of research about how game engines work in general.<br></br>
            </p>
            <p>
                Then I went into a Game Programming School at <a href="https://www.forsbergsskola.se/utbildningar/game-programming/">Forsbergs</a> to get a degree so i could do what i love for a living.<br></br>
            </p>
            <p>
                I also have experience with other things such as Art, <a href='https://open.spotify.com/artist/6SAVAQx61cKUKHbIFq2v7g?si=GrjVr2-pR1i_fv2XZmreqg' target='_blank'>Sound</a>, Animations, UI.
            </p>
            
            <img className='man' src={man}></img>

            <iframe className='spotify'
                src="https://open.spotify.com/embed/track/77SDsNJZ8N3Tljx59dXXFW?utm_source=generator" 
                width="30%" height="80" frameBorder="0" allowFullScreen=""
                allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture">
            </iframe>
        </div>
    </div>
}

export default Home;