

let stars = [];
let amountOfStars = 1000;

function GetRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min) + min);
}

function SpawnStars(){
    let STARCONTAINER = document.getElementById("starContainer");

    console.log(STARCONTAINER);

    for(let i = 0; i < amountOfStars; i++){
        let star = document.createElement("div");
        star.className = "star";
        let randomSize = GetRandomInt(1, 5);
        star.style.width = `${randomSize}.px`;
        star.style.height = `${randomSize}.px`;
        star.style.top = `${GetRandomInt(-2000, 2000)}.px`;
        star.style.left = `${GetRandomInt(-2000, 2000)}.px`;
        star.style.animation = `background-stars-animation ${GetRandomInt(10, 100)}s linear infinite`;
        STARCONTAINER.appendChild(star);
        stars.push(star);
    }
}

function Main() {
    setTimeout(SpawnStars, 2);
}

Main();