import React from "react";

import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";

import "./portfolio.css";

import violent_screenshot1 from "../Assets/Portfolio/Violent/Screenshot1.png";
import violent_screenshot2 from "../Assets/Portfolio/Violent/Screenshot2.png";
import violent_screenshot3 from "../Assets/Portfolio/Violent/Violent_Compressed_ALOT_SparkVideo.gif";

import flappywall_screenshot1 from "../Assets/Portfolio/Flappy Wall/1.gif";
import flappywall_screenshot2 from "../Assets/Portfolio/Flappy Wall/2.png";
import flappywall_screenshot3 from "../Assets/Portfolio/Flappy Wall/3.png";
import flappywall_screenshot4 from "../Assets/Portfolio/Flappy Wall/4.png";

function ShowPortfolioPiece(div){
  const divs = ['Solidriver', 'FlappyWall', 'Violent', 'More']

  for(let i = 0; i < divs.length; i++){
    if(divs[i] == div){
      document.getElementById(divs[i]).style.display='block'  
    }
    else{
      document.getElementById(divs[i]).style.display='none'
    }
  }
}

function EnlargeImage(id){
  let img = document.getElementById(id);
  
  if(img.style.transform == "scale(1.5)"){
    img.style.transform = "scale(1)";
    img.style.transition = "transform 0.25s ease";
    img.style.zIndex = '0';
    img.style.position = 'relative';
  }
  else{
    img.style.transform = "scale(1.5)";
    img.style.zIndex = '1';
    img.style.position = 'relative';
    img.style.transition = "transform 0.25s ease";
  }
}

function Portfolio() {
  return (
    <div>
      <div className="portfolionavigator">
        <ul>
          <li><button onClick={() => ShowPortfolioPiece('Solidriver')}>Solid River</button></li>
          <li><button onClick={() => ShowPortfolioPiece('Violent')}>Violent</button></li>
          <li><button onClick={() => ShowPortfolioPiece('FlappyWall')}>Flappy Wall</button></li>
          <li><button onClick={() => ShowPortfolioPiece('More')}>More</button></li>
        </ul>
      </div>

      <div id='Solidriver' className="Solidriver">
        <div className="portfoliopiece">
          <h1>Solid River</h1>
          <h3 style={{marginTop:'-20px'}}>I did a 9 month internship at <a href='https://welevel.com/' target='_blank' rel='noreferrer'>WeLevel</a> as part of my education at <a href="https://www.forsbergsskola.se/utbildningar/game-programming/" target='_blank'>Forsbergs</a></h3>
          <br></br>
          <div className='steamdiv'>
            <iframe title="steam" className='steam' src="https://store.steampowered.com/widget/1551260/" frameBorder="0" width="646" height="190"></iframe>
          </div>
          
          <p>I did a whole bunch of everything programming related. Like: Physics, Gameplay, UI and a lot more.
          <br></br>  It was fun and the team was nice.
          </p>
          
        </div>
      </div>

      <div id="More" className="More" style={{display: 'none'}}>
        <h3>
          <div className="portfoliopiece">
            <h1>More will be added...</h1>
            <p>This website is a WIP</p>
            <p>
              I have a lot more projects to show here in the future. <br></br>
              But I have yet to make any presentable material out of it like videos, screenshots etc.
            </p>
          </div>
        </h3>
      </div>

      <div id="FlappyWall" className="FlappyWall" style={{display: 'none'}}>
        <div className="portfoliopiece">
          <h1>Flappy Wall</h1>
          <h3>A hyper-casual mobile game made in Unity</h3>
          <h4 style={{textAlign:'center', marginTop:'-15px'}}>(To be released in the beginning of 2023)</h4>
          <img alt="" id='flappywall1' className="screenshotphone" src={flappywall_screenshot1}></img>
          
          <p>
            Simple but captivating gameplay<br></br>
            The game procedurally gets harder as you play, the bird becomes faster and there can be more spikes on the walls.<br></br>
            The colors on the playing field changes every 50 points.
          </p>

          <br></br>

          <img alt="" id='flappywall2' className="screenshotphone" src={flappywall_screenshot2}></img>
          <br></br>
          <img alt="" className="screenshotphone" src={flappywall_screenshot3}></img>
          <p>
            The player can choose to watch an AD to get 2x the gems. <br></br>
            The gems are used to buy different bird skins.
          </p>

          <br></br>
          <img alt="" className="screenshotphone" src={flappywall_screenshot4}></img>

        </div>
      </div>

      <div id="Violent" className="Violent" style={{display: 'none'}}>
        <div className="portfoliopiece">
          <h1>Violent</h1>
          <h3>An old-school shooter game i solo-developed in Unreal</h3>

          <img alt="" id='violent1' className="screenshot" onClick={() => EnlargeImage('violent1')} src={violent_screenshot1}></img>

          <p>
            I really wanted to capture the oldschool feeling, so i made sprites
            that would look like Doom,
            They are made by taking a photo and then pixelating them.<br></br>
            The levels are whiteboxed and the 3D models are modelled by me, the textures are CC0 textures with a shader I made in Unreal.
          </p>
          
          <br></br>
          <img alt="" id='violent2' className="screenshot" onClick={() => EnlargeImage('violent2')} style={{zIndex:'-'}} src={violent_screenshot2}></img>
          <br></br>
          <img alt="" id='violent3' className="screenshot" onClick={() => EnlargeImage('violent3')} src={violent_screenshot3}></img>
          <br></br>
          <p>There are 3 weapons in the game and 1 tool</p>
          <p>
            1. RockGun, shoot a rock , if you alt-fire when a rock exists the
            rock will split into multiple smaller rocks.
          </p>
          <p>2. Wand, shoot a slow moving fireball with no gravity.</p>
          <p>
            3. Pipebomb, throw a pipebomb, if you alt-fire while any exist they
            will explode.
          </p>
          <p>
            The tool is the lantern, you can only carry 1, and you can drop it
            to light up your current area, you can also kick it.
          </p>
          <br></br>
          <iframe
            className='youtube'
            width="1200"
            height="804"
            src="https://www.youtube.com/embed/JZEa6HB3d-s"
            title="Violent - Weapon Test"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </div>
      </div>
    </div>
  );
}
export default Portfolio;
